
//Small device
@mixin sd {
  @media (min-width: #{$screen-sd}){
    @content;
  }
}

//Medium device
@mixin md {
  @media (min-width: #{$screen-md}){
    @content;
  }
}

//Large device
@mixin lg {
  @media (min-width: #{$screen-lg}){
    @content;
  }
}

//XL device
@mixin xl {
  @media (min-width: #{$screen-xl}){
    @content;
  }
}

//Custom
@mixin rwd($screen) {
  @media (min-width: $screen+'px'){
    @content;
  }
}