.menu{
    position: absolute;
    right: 0;
    &__liste{
        padding: 20px;
        margin: 0;
        list-style: none;
        display: flex;
    }
    &__el{
        margin-bottom: 20px;
        
        a{
            text-decoration: none;
            color: $c-blue;
            font-weight: bold;
        }
    }
    &__btn{
        color: #fff;
        background-color: $c-purple;
        text-decoration: none;
        padding: 10px 20px;
        border-radius: 6px;
    }
    .menu__el--last{
        margin-bottom: 35px;
    }
}

@media (max-width: 800px){
    .menu{
        top: 65px;
        padding: 10px;
        transform: scale(0);
        box-shadow: 2px 4px 30px rgba(0,0,0,.3);
        border-radius: 10px;
        
    &__liste{
        padding: 20px;
        margin: 0;
        list-style: none;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
    &__el a{
        color: #fff;
    }
    }
    .menu--open{
        transform: scale(1);
        transform-origin: right;
        transition: .5s;
        background-color: $c-red; 
        &--btn{
            margin-top: 30px;
        }
    }
}

@media (min-width: 800px) {
    .burger-menu, .burger-menu--closed{
        display: none;
    }
    .menu{
        display: block;
        &__liste{
            flex-direction: row;
            padding: 25px;
        }
        &__el{
            margin-right: 50px; 
            & a{
                &:hover, &:focus{
                    color: $c-darkblue;
                    transition: .4s ease-out;
                }
            }
        }
        &__btn{
            &:hover, &:focus{
            background-color: $c-red;
            transition: .4s ease-out;
            }  
        }
    }
    
}
@include xl {
     .menu{
        &__liste{
            padding: 50px;
        }
        &__el{
            margin-right: 75px; 
         }
    }
}
