body {
    font-family: 'Lato', sans-serif; 
    font-size: 17px;
    line-height: 1.5;
    color: $c-darkblue;
}
p{
    margin: 25px 0 0 0;
    max-width: 600px;
}
// ----- TYPO -----
.title--big {
    color: $c-darkblue;
    height: 175px;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    display: block;
    margin: 0;
    background: {
        image: url(../../assets/images/logo.svg);
        repeat: no-repeat;
        position: center;
        size: contain;
    }
}
.title--large{
    font-family: 'Abril Fatface', cursive;
    font-size: 34px;
    font-weight: normal;
    line-height: 1.3;
    margin: 0;
    padding-top: 75px;
    max-width: 700px;
}
.title--medium{
    font-size: 24px;
    line-height: 1.4;
    font-weight: 900;
    margin: 0;
    padding-top: 50px;
}
.title--intro{
    color: #fff;
    position: absolute;
    display: block;
    text-align: center;
    vertical-align: middle;
    max-width: 600px;
}
.title--light{
    color: #fff;
}
// ----- HEADER -------
.header{
    height: 100vh;
    
    .container__plante--big{
        position: absolute; top: 0; left: 0;
        width: 50%;
        height: 250px;
        animation: planteBig 4s ease-out;
        transform-origin: left top;
            background:{
            image: url(../../assets/images/plantes1.svg);
            position: left top;
            repeat: no-repeat;
            size: contain;
            }
    }
    @keyframes planteBig {
        from{
            transform: scale(0) translate(-25px, -25px);
        }
        to{
            transform: scale(1) translate(0);
        }
    }
    .container__title{
        max-width: 450px;
        margin: 0 auto;
//        padding-top: 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding: 0 50px;
        .title__txt{
            text-align: center;
        }
    }
    .container__plante{
        height: 150px;
        width: 150px;
        position: absolute; bottom: 25px; right: 0px;
        overflow-x: hidden;
        background-image: url(../../assets/images/plantes2.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: bottom;
        animation: plante 4s ease-out;
        transform-origin: right bottom;
    }
    @keyframes plante {
        from{
            transform: scale(0) translate(25px, 25px);
        }
        to{
            transform: scale(1) translate(0);
        }
    }
    .container__vague{
        display: block;
        width: 100%;
        z-index: 10;
        height: 200px;
        position: absolute;
        bottom: 0;
        background:{
            image: url(../../assets/images/vague-big.svg);
            repeat: repeat-x;
            position: left bottom;
            size: 500%;
        }
        animation: wave 40s infinite linear;
    }
        @keyframes wave{
            from{
                background-position: left bottom;
            }
            to{
                background-position: right bottom;
            }
        }
    .fadeInDown{
        animation: fadeInDown 1.5s;
    }
    .fadeInUp{
        animation: fadeInUp 1.5s;
    }
    @keyframes fadeInDown{
        from{
            opacity: 0;
            transform: translate3d(0, -10%, 0);
        }
        to{
            opacity: 1;
            transform: none;
        }
    }
     @keyframes fadeInUp{
        from{
            opacity: 0;
            transform: translate3d(0, 20%, 0);
        }
        to{
            opacity: 1;
            transform: none;
        }
    }
}

em{
    background-image: linear-gradient(to bottom,$c-red,$c-red);
    color: #fff;
    font-style: normal;
}
// ----- SECTIONS -----
.section{
    padding: 0 25px 75px 25px;
    box-sizing: border-box;
}
// ------ SECTION INTRO ------
.section--intro{
    background-color: $c-darkblue;
    position: relative;
    margin-top: -2px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    min-height: 600px;
    
    
    .txtAnim{
        position: relative;
        display: block;
        margin: 0 auto;
        width: 100%;

        .anim__el{
            text-align: center;
            opacity: 0; 
            animation: showText 25s infinite linear;
            position: relative;
            &:before{
                content: attr(data-lettrine);
                color: $c-blue;
                opacity: .8;
                font-size: 17px;
                line-height: 1;
                font-family: 'Lato', sans-serif; 
                display: block;
                position: absolute;
                z-index: -1;
                bottom: -33px;
                left: 50%;
                transform: translateX(-50%);
                }
        }
        .anim__el:first-child{
        }
        .anim__el:nth-child(2){  
             animation-delay: 6s;
             position: absolute;
             top: 0;
        }
        .anim__el:nth-child(3){
             animation-delay: 12s;
             position: absolute;
             top: -33px;
        }
        .anim__el:last-child{
             animation-delay: 19s;
             position: absolute;
             top: 0;
        }
        @keyframes showText {
            5% {
                opacity: 0;
            }
            11% {
                opacity: 1;
            }
            27% {
                opacity: 1;
            }
            34% {
                opacity: 0;
            }
        }
    }
    
    .poisson{
        position: absolute;
        animation: upAndDown 8s infinite linear;
        background: {
            image: url(../../assets/images/poisson2.svg);
            repeat: no-repeat;
            position: right top;
        }
        height: 170px;
        width: 100px;
        right: 25px;
        bottom: 40px;
        @keyframes upAndDown{
            0%{
               transform: translateY(0px);
            }
            25%{
                transform: translateY(6px);
            }
            75%{
                transform: translateY(-6px);
            }
            100%{
                transform: translateY(0px);
            }
        }
    }
    .bouteille{
        position: absolute;
        background: {
            image: url(../../assets/images/bouteille.svg);
            repeat: no-repeat;
        }
    }
    .sac{
        position: absolute;
        background: {
            image: url(../../assets/images/sac.svg);
            repeat: no-repeat;
        }
    }
    .sac--big{
        display: none; 
        animation: sacbig 15s infinite linear;
        @keyframes sacbig{
            0%{
               transform: translate(0px, -30px) rotate(0deg); 
            }
            25%{
                transform: translate(15px, -15px) rotate(-10deg);
            }
            50%{
                transform: translate(0) rotate(-5deg);
            }
            75%{
                transform: translate(-15px, -15px) rotate(-10deg);
            }
            100%{
                transform: translate(0px, -30px) rotate(0deg);
            }
        }
    }   
    .sac--small{
        height: 110px;
        width: 110px;
        right: 60px;
        top: 60px;  
        animation: sacSmall 10s infinite linear;
        @keyframes sacSmall{
            0%{
               transform: translate(0) scale(-1, 1);
            }
            25%{
                transform: translate(4px, 10px) scale(-1, 1);
            }
            75%{
                transform: translate(-15px, 20px) scale(-1, 1);
            }
            100%{
                transform: translate(0) scale(-1, 1);
            }
        }
    }
        
    .bouteille--big{
        display: none;
        
        height: 120px;
        width: 50px;
        left: 300px;
        top: 150px;
        animation: boutbig 15s infinite linear;
          @keyframes boutbig{
            0%{
               transform: translate(0) rotate(-70deg);
            }
            25%{
                transform: translate(-15px, -15px) rotate(-75deg);
            }
            50%{
                transform: translate(0px, -30px) rotate(-70deg); 
            }
            75%{
                transform: translate(15px, -15px) rotate(-65deg);
            }
            100%{
                transform: translateX(0) rotate(-70deg);
            }
        }
    }
    .bouteille--small{
        height: 80px;
        width: 40px;
        left: 50px;
        bottom: 100px;
        animation: boutSmall 10s infinite linear;
        @keyframes boutSmall{
            0%{
               transform: translate(0) rotate(140deg);
            }
            25%{
                transform: translate(-10px, 15px) rotate(150deg);
            }
            75%{
                transform: translate(4px, 10px) rotate(130deg);
            }
            100%{
                transform: translate(0) rotate(140deg);
            }
        }
    }
    

}
// ----- SECTION ZD -----
.section--zd{
    background: {
        color: $c-lightblue;
        image: url(../../assets/images/vague.svg);
        position: top;
        size: 102%;
        repeat: no-repeat;
    } 
}
.zd__top{
    padding: 0 25px 100px 25px;
    box-sizing: border-box;
    width: 100%;
    margin-top: -1px;
    
}
.zd__bottom{
    z-index: 10;
    position: relative;
    padding: 0 25px 75px 25px;
    box-sizing: border-box;
    background: {
        color: $c-darkblue; 
    } 
    .container__iceberg{
        display: flex;
        justify-content: center;
    }
    .iceberg{
        margin-top: -74px;
        height: 300px;
        z-index: 20;
    }
    ul{
        padding-left: 0;
    }
}
// ----- SECTION 3 -----
.section--iphone{
    margin-top: -1px;
    background: {
        color: $c-red;
        image: url(../../assets/images/vague.svg);
        position: top;
        size: 102%;
        repeat: no-repeat;
    }
    .iphone{
        height: 80%;
        margin-top: 50px;
        background:{
            image: url(../../assets/images/iphone.svg);
            position: center;
            repeat: no-repeat;
        }
    }
}
// ----- FOOTER -----


.footer{
    text-align: center;
    padding: 125px 25px 25px 25px;
    position: relative;
    margin-top: -1px;
    .container__vague{
        position: absolute; top: 0; left: 0;
        width: 100%;
        height: 100px;
        z-index: 2;
        background-image: url(../../assets/images/vague-red.svg);
        background-repeat: no-repeat;
        background-size: 101%;
        background-position: top;
    }
    .container__plante{
        position: absolute; 
        background-repeat: no-repeat;
        background-size: contain;
        background-position: top;
    }
    .container__plante--top{
        z-index: 1;
        height: 150px;
        width: 150px;
        overflow-x: hidden;
        background-image: url(../../assets/images/plantes1.svg);
        top: 0px; left: 0px;
    }
    .container__plante--bottom{
        display: none;
        position: absolute; bottom: 0px; right: 0px;
    }
    .link__btn{
        color: #fff;
        display: inline-block;
        background-color: $c-purple;
        text-decoration: none;
        padding: 10px 20px;
        width: 150px;
        box-sizing: border-box;
        border-radius: 6px;
        &:hover, &:focus{
            background-color: $c-red;
            transition: .4s ease-out;
        }
    }
    &__txt{
        margin: 0 auto;
        padding: 110px 0 100px 0;
    }
    &__link{
        color: inherit;
        text-decoration: none;
        &:hover, &:focus{
             font-weight: bold;
        }
    }
}




