@media (max-width: 800px){
    
//    source : https://codepen.io/fabiowallner/pen/wzJyoP
        .burger-menu {
            z-index: 100;
        margin: 0 auto;
        width: 30px;
        display: block;
        transition: all 0.3s;
        cursor: pointer;
        height: 35px;
        position: absolute; top: 20px; right: 20px;

     & .bar {
        transition: all 0.3s;
        height: 4px;
        border-radius: 4px;
        width: 100%;
        display: block;
        background-color: #582fb3;
    }
     & .bar:nth-of-type(2) {
        margin: 8px 0;
    }
     &--closed {
        transition-delay: 0.3s;
    }
     &--closed .bar {
        float: right;
    }
     &--closed .bar:nth-of-type(2) {
        width: 100%;
        transition-property: margin, height, width;
        transition-delay: 0.3s, 0.3s, 0s;
    }
     &--closed .bar:nth-of-type(3) {
        width: 100%;
    }
     &--opened {
        padding-top: 12px;
    }
     &--opened .bar:nth-of-type(1) {
        -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
        transition-delay: 0.3s;
        height: 4px;
    }
     &--opened .bar:nth-of-type(2) {
        opacity: 0;
        height: 0;
        margin: -4px;
    }
     &--opened .bar:nth-of-type(3) {
        -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
        transition-delay: 0.3s;
        height: 4px;
    }
    }


}
@media (min-width: 800px){
    .header{
        background-size: 30%;
    }
}
// ----- Style desktop 
@include lg {
    
    .header{
        background-size: 25%;
        .title--big + p{
            margin: 0;
        }
        .container__vague{
        background:{
            size: 280%;
            }
        }
        .container__plante{
            bottom: 75px;
            height: 275px;
            width: 275px;
        }
    }
    .burger-menu, .burger-menu--closed{
        display: none;
    }
    .section--intro{
        padding: 50px 100px 150px 100px;
        .bouteille--big{
            left: 130px;
        }
        .bouteille--small{
            left: 450px;
            bottom: 90px;
        }
        .sac--big{
            left: 750px;
            bottom: 10px;
        }
        .poisson{
            bottom: 140px;
            right: 75px;
            height: 240px;
            width: 130px;
        }
    }
    .zd__top, .zd__bottom{
        
        display: grid;
        grid-template-columns: repeat(8, 1fr);
        grid-gap: 25px;
    }
    .zd__top{
        padding: 100px 100px 75px 100px;
        .title--large{
            grid-column: 1 / 5;
            grid-row: 1;
        }
        .left{
            grid-column: 2 / 6;
            grid-row: 2;
        }
        .right{
            grid-column: 5 / 9;
            grid-row: 3;
        }
    }
    .zd__bottom{
        padding: 25px 100px 200px 100px;
        .container__iceberg{
            grid-column: 1 / 5;
            grid-row: 1 / 3;
            
            .iceberg{
                margin-top: -148px;
                height: 500px;
            }
        }
        .bloc1{
            grid-column: 5 / 9;
            grid-row: 1;
        }
        .bloc2{
            grid-column: 5 / 9;
            grid-row: 2 / 3;
        }
        .bloc3{
            grid-column: 2 / 6;
            grid-row: 3;
        }
        .iceberg{
            margin-top: -141px;
        }
    }
    .section--iphone{
        padding-top: 125px;
        height: inherit;
        display: grid;
        grid-template-columns: repeat(8, 1fr);
        grid-gap: 25px;
        
        .title--large{
            grid-column: 2 / 8;
            grid-row: 1;
        }
        .container__flex{
            margin-top: 125px;
            grid-row: 1;
            grid-column: 2 / 8;
            
        }
        .left{
            display: inline-block;
            width: 40%;
           
        }
        .iphone{
            margin-top: -50px;
            height: 400px;
            width: 50%;
     
        }
    }
    .footer{
        box-sizing: border-box;
        padding-top: 200px;
        &__txt{
            max-width: 35%;
            padding-bottom: 200px;
        }
        .container__plante--top{
            height: 350px;
            width: 350px;
        }
        .container__plante--bottom{
            height: 300px; width: 300px;
        }
    }
    
}
